.App {
  font-family: sans-serif;
  text-align: center;
}
.TopBanner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 30px;
}
.logo {
  color: red;
  font-size: 17px;
}
.TopBannerFeatures {
  padding: 10px;
  background-color: orangered;
  color: white;
  border-radius: 30px;
}
.contentContainer {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-gap: 10px;
  overflow: hidden;
}
.SidebarContainer {
  min-height: 100vh;
  width: 100%;
}
.SidebarActions {
  list-style: none;
}
.SidebarActionsLink {
  text-align: center;
  margin: 10px 0px;
}
.VideosContainer {
  margin-top: 20px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
.video {
  border: 2px solid grey;
  padding: 10px;

  transition: 0.4s all cubic-bezier(0.165, 0.84, 0.44, 1);
}
.video:hover {
  transform: translateY(-5px);
  background-color: gainsboro;
}
.videoTitle {
  font-size: 12px;
  color: black;
}
.videoBody {
  font-size: 8px;
  color: grey;
}
.filterContainer {
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.filterContainerPara {
  margin: 0px 10px;
  background-color: whitesmoke;
  padding: 8px;
  border-radius: 20px;
  font-size: 11px;
  color: black;
  border: 1px solid grey;
}

@media (max-width: 1000px) {
  .VideosContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 700px) {
  .VideosContainer {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
  }
  .filterContainer {
    display: none;
  }
}

@media (max-width: 500px) {
  .contentContainer {
    display: block;
  }
  .SidebarContainer {
    display: none;
  }
  .VideosContainer {
    grid-template-columns: repeat(1, 1fr);
  }
}
